<template>
	<div>
		<Loader v-if="isLoading" />
		<v-data-table
			v-else
			class="mt-6"
			:headers="header"
			:items="dataBerita"
			:items-per-page="10">
			<template v-slot:no-data>
	      <v-alert type="info" class="mt-4">
	        <div>Data tidak ditemukan</div>
	      </v-alert>
	    </template>
	    <template v-slot:item.no="{item}">
	    	{{dataBerita.map(x => x.id).indexOf(item.id)+1}}    	
	    </template>
			<template v-slot:item.action="{item}">
				<v-btn icon color="success">
					<v-icon small>mdi-eye</v-icon>
				</v-btn>
				<v-btn 
					@click="$router.push({name: 'edit-berita', params: { id: item.id }})"
					icon 
					color="primary">
					<v-icon small>mdi-pencil</v-icon>
				</v-btn>
				<HapusBerita :id="item.id" />
			</template>		
		</v-data-table>
	</div>
</template>

<script>
	import { mapState } from 'vuex'
	import Loader from '@/components/Loader'
	import HapusBerita from './HapusBerita'

	export default{
		data: () => ({
			header: [
				{ text: 'No', value: 'no', sortable: false },
				{ text: 'Judul', value: 'judul', sortable: false },
				{ text: 'Kategori', value: 'kategori.nama', sortable: false },
				// { text: 'Tanggal Publish', value: 'pass', sortable: false },
				{ text: 'Action', value: 'action', align: 'center', sortable: false }
			],

			data: [
				{
					name: 's',
					user: 's',
					pass: 's'
				}
			],
		}),

		components: {
			Loader,
			HapusBerita
		},

		created() {
			this.$store.dispatch('dataBerita/getDataBerita')
			this.$store.dispatch('dataBerita/updateIsLoading', true)
		},

		computed: { ...mapState('dataBerita', ['dataBerita', 'isLoading']) }
	}
</script>