<template>
  <v-dialog
    v-model="dialog"
    width="500"
    persistent>
    <template v-slot:activator="{ on }">
      <v-btn 
        depressed
        v-on="on"
        :loading="isLoading"
        class="mr-3"
        color="success">
        <v-icon class="mr-3">mdi-plus-circle</v-icon>
        Tambah kategori
      </v-btn>
    </template>

    <v-card>
      <v-card-title class="py-2">
        Tambah Kategori
      </v-card-title>
      <v-divider></v-divider>
      <v-form v-model="valid" ref="form">
        <v-card-text class="pa-6 pb-0">
          <v-text-field    
            v-for="(item, i) in dataKategori"
            :key="i"
            v-model="item.nama"        
            label="Nama kategori"
            outlined
            dense
            :rules="requiredRule"
            autocomplete="off"
            append-outer-icon="mdi-minus"
            @click:append-outer="minus(item.id)"
          ></v-text-field> 
          <v-text-field
            v-model="data.nama"            
            label="Nama"
            outlined
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-text-field>         
          <v-btn
            :disabled="!valid"
            color="primary"
            :loading="isLoading"
            @click.prevent="validate"
            type="submit"
            block
            class="mb-6"
            depressed>
            tambah kategori
          </v-btn>
         
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="primary"
            text
            @click="dialog = false">
            kembali
          </v-btn>
        </v-card-actions>
      </v-form>
    </v-card>
  </v-dialog>
</template>

<script>
  import { mapState } from 'vuex'

  export default {
    data () {
      return {
        dialog: false,
        valid: true,
        data: {
          nama: ''
        },
        requiredRule: [v => !!v || 'Data harus diisi'],
      }
    },

    created() {
      this.$store.dispatch('dataKategori/getDataKategori')
      this.$store.dispatch('dataKategori/updateIsLoading', true)
    },

    computed: { 
      ...mapState('dataKategori', ['dataKategori', 'isLoading'])
    },

    methods: {
      validate () {
        if (this.$refs.form.validate()) {
          this.$store.dispatch('dataKategori/postDataKategori', this.data)
          this.$store.dispatch('dataKategori/updateIsLoading', true) 
          // this.data.nama = ''         
        }
      },
      minus(val) {
        this.$store.dispatch('dataKategori/deleteDataKategori', val)
        this.$store.dispatch('dataKategori/updateIsLoading', true)
      }
    }
  }
</script>
